import React from 'react'
import Article from './ArticleItem'
import './article-list.scss'

const Articles = ({ data }) => {
    return (
        <div className="articles">
            {data.map( article => {
                    const data = article.node
                    return <Article key={data.id} data={data} image={data.image.localFile} /> 
                }
            )}
        </div>
    )
}

export default Articles