import React from "react"
import { graphql } from 'gatsby'

import Layout from "/src/layouts/main/layout/layout.js"
import Seo from "../components/seo"
import ArticleList from '../components/ArticleList'


const ArticlesPage = ({ data }) => {
  
  return (
    <Layout>
      <Seo title="Articles" />
      {<ArticleList data={data.allStrapiArticle.edges} />}
    </Layout>
  )
}

export default ArticlesPage

export const pageQuery = graphql`query ArticlesQuery {
  allStrapiArticle {
    edges {
      node {
        id
        image {
          localFile {
            childImageSharp {
              gatsbyImageData(
                layout: CONSTRAINED
                transformOptions: {}
                width: 400
                height: 225
              )
            }
          }
        }
        title
        content
        published_at
        author {
          id
          username
        }
      }
    }
  }
}
`