import React from 'react'
import { Link } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";
import ReactMarkdown from "react-markdown";
import {string_to_slug} from "/src/core/utils";

const Article = ({ data, image }) => {
    
    return (
        <Link className="articles-item__link" to={`/articles/${string_to_slug(data.title)}`}>
            <div className="articles-item">            
                <div className="articles-item__img-wrapper">
                    { image ? <GatsbyImage image={image.childImageSharp?.gatsbyImageData} alt="" className="articles-item__img" /> : '' }
                </div>            
                <div className="articles-item__content">
                    <h3 className="articles-item__title">
                        {data.title}
                    </h3>
                    <p className="articles-item__descripcion">
                        <ReactMarkdown
                            source={data.content}
                            escapeHtml={false}
                        />
                    </p>
                    <p className="articles-item__footer">{data.published_at}</p>
                </div>            
            </div>
        </Link>
    );

}

export default Article